import { MotionSensor, Setting, Settings, SettingValue } from "@scrypted/sdk";
import { SettingsMixinDeviceBase, SettingsMixinDeviceOptions } from "@scrypted/sdk/settings-mixin";
import { StorageSettings } from "@scrypted/sdk/storage-settings";
import { getBaseLogger, logLevelSetting } from '../../scrypted-apocaliss-base/src/basePlugin';
import FrigateBridgeMotionDetector from "./motionDetector";
import { initFrigateMixin } from "./utils";

export class FrigateBridgeMotionDetectorMixin extends SettingsMixinDeviceBase<any> implements Settings, MotionSensor {
    storageSettings = new StorageSettings(this, {
        logLevel: {
            ...logLevelSetting,
        },
        cameraName: {
            title: 'Frigate camera name',
            type: 'string',
            choices: [],
            immediate: true,
        },
        motionTimeoutSeconds: {
            title: 'Motion timeout in seconds',
            type: 'number',
            defaultValue: 15
        },
        reportMotionOnlyOnDetection: {
            title: 'Report motion only on detection (Requires the Frigate object detector extension)',
            type: 'boolean',
            description: 'Whether to report motion only on detection events. Useful if you want to notify motion to homekit only when any detection happens',
            defaultValue: false,
            immediate: true,
        },
    });

    logger: Console;
    motionTimeout: NodeJS.Timeout;

    constructor(
        options: SettingsMixinDeviceOptions<any>,
        public plugin: FrigateBridgeMotionDetector
    ) {
        super(options);

        this.plugin.currentMixinsMap[this.id] = this;

        const logger = this.getLogger();
        this.init().catch(logger.error);
    }

    async init() {
        await initFrigateMixin({
            mixin: this,
            storageSettings: this.storageSettings,
            plugin: this.plugin.plugin,
            logger: this.getLogger(),
        });
    }

    async onFrigateMotionEvent(value: 'ON' | 'OFF' | string) {
        const logger = this.getLogger();
        logger.log('Motion update received', value);

        const { motionTimeoutSeconds } = this.storageSettings.values;

        const newMotionValue = value === 'ON';
        if (newMotionValue !== this.motionDetected) {
            this.motionDetected = newMotionValue;
        }

        if (this.motionDetected) {
            this.motionTimeout && clearTimeout(this.motionTimeout);
            this.motionTimeout = undefined;

            this.motionTimeout = setTimeout(() => {
                this.motionDetected = false;
            }, (motionTimeoutSeconds || 15) * 1000);
        }
    }

    async getMixinSettings(): Promise<Setting[]> {
        const logger = this.getLogger();
        try {
            this.storageSettings.settings.cameraName.choices = this.plugin.plugin.storageSettings.values.cameras;

            return this.storageSettings.getSettings();
        } catch (e) {
            logger.log('Error in getMixinSettings', e);
            return [];
        }
    }

    async putSetting(key: string, value: SettingValue): Promise<void> {
        const [group, ...rest] = key.split(':');
        if (group === this.settingsGroupKey) {
            this.storageSettings.putSetting(rest.join(':'), value);
        } else {
            super.putSetting(key, value);
        }
    }

    async putMixinSetting(key: string, value: string) {
        this.storageSettings.putSetting(key, value);
    }

    async release() {
        const logger = this.getLogger();
        logger.info('Releasing mixin');
    }

    getLogger() {
        if (!this.logger) {
            this.logger = getBaseLogger({
                console: this.console,
                storage: this.storageSettings,
            });
        }

        return this.logger;
    }
}
