import { MixinProvider, ScryptedDeviceBase, ScryptedDeviceType, ScryptedInterface, SettingValue, WritableDeviceState } from "@scrypted/sdk";
import { StorageSettings, StorageSettingsDict } from "@scrypted/sdk/storage-settings";
import { getBaseLogger, getMqttBasicClient, logLevelSetting } from '../../scrypted-apocaliss-base/src/basePlugin';
import MqttClient, { MqttMessageCb } from "../../scrypted-apocaliss-base/src/mqtt-client";
import FrigateBridgePlugin from "./main";
import { FrigateBridgeObjectDetectorMixin } from "./objectDetectorMixin";
import { activeTopicWildcard, eventsTopic, FRIGATE_OBJECT_DETECTOR_INTERFACE, FrigateEvent, objectCountTopicWildcard, parseMqttCountPayload } from "./utils";

export default class FrigateBridgeObjectDetector extends ScryptedDeviceBase implements MixinProvider {
    initStorage: StorageSettingsDict<string> = {
        logLevel: {
            ...logLevelSetting,
        },
    };
    storageSettings = new StorageSettings(this, this.initStorage);
    currentMixinsMap: Record<string, FrigateBridgeObjectDetectorMixin> = {};
    plugin: FrigateBridgePlugin;
    logger: Console;
    mqttCb: MqttMessageCb;
    initializingMqtt = false;
    public mqttClient: MqttClient;

    constructor(nativeId: string, plugin: FrigateBridgePlugin) {
        super(nativeId);
        this.plugin = plugin;
        this.startStop(this.storageSettings.values.pluginEnabled).then().catch(this.getLogger().log);
    }

    async startStop(enabled: boolean) {
        if (enabled) {
            await this.start();
        } else {
            await this.stop();
        }
    }

    async stop() {
        const logger = this.getLogger();
        logger.log('Stopping ObjectDetector listeners');
        await this.mqttClient?.disconnect();
    }

    async start() {
        try {
            const logger = this.getLogger();
            logger.log('Starting ObjectDetector listeners');
            await this.startMqttListener();
        } catch (e) {
            this.getLogger().log(`Error in initFlow`, e);
        }
    }

    async startMqttListener() {
        const mqttClient = await this.getMqttClient();
        const logger = this.getLogger();

        const getEventZones = (obj: FrigateEvent): string[] => {
            const zones = obj?.after?.current_zones ?? obj?.before?.current_zones;
            return Array.isArray(zones) ? zones : [];
        };

        const shouldForwardByZonePrefilter = (mixin: FrigateBridgeObjectDetectorMixin | undefined, obj: FrigateEvent): boolean => {
            if (!mixin)
                return true;

            const eventZones = getEventZones(obj);
            const zoneNames: string[] = mixin.storageSettings.values?.zones ?? [];

            const includeZones: string[] = [];
            const excludeZones: string[] = [];

            for (const zoneName of zoneNames) {
                const typeKey = `zone:${zoneName}:type`;
                const type = mixin.storageSettings.getItem(typeKey) ?? 'Default';
                if (type === 'Include')
                    includeZones.push(zoneName);
                else if (type === 'Exclude')
                    excludeZones.push(zoneName);
            }

            // If any includes exist, only those pass (include takes precedence over exclude).
            if (includeZones.length)
                return eventZones.some(z => includeZones.includes(z));

            // Otherwise, if excludes exist, block those.
            if (excludeZones.length)
                return !eventZones.some(z => excludeZones.includes(z));

            // All default -> always forward.
            return true;
        };

        // const updateCounts = (current: FrigateActiveTotalCounts | undefined, patch: Partial<FrigateActiveTotalCounts>): FrigateActiveTotalCounts => {
        //     return {
        //         active: current?.active ?? 0,
        //         total: current?.total ?? 0,
        //         ...patch,
        //     };
        // }

        const getCameraZonesMap = (): Record<string, string[]> => {
            const raw: any = this.plugin?.storageSettings?.values?.cameraZones;

            if (!raw)
                return {};

            if (typeof raw === 'string') {
                try {
                    return JSON.parse(raw);
                } catch {
                    return {};
                }
            }

            return raw as Record<string, string[]>;
        };

        const getCameraMixinsForZone = (zoneName: string): FrigateBridgeObjectDetectorMixin[] => {
            if (!zoneName)
                return [];

            const cameraZones = getCameraZonesMap();
            const cameraNames = Object.entries(cameraZones)
                .filter(([_, zones]) => Array.isArray(zones) && zones.includes(zoneName))
                .map(([cameraName]) => cameraName);

            if (!cameraNames.length)
                return [];

            const mixins = Object.values(this.currentMixinsMap)
                .filter(mixin => cameraNames.includes(mixin.storageSettings.values.cameraName));

            return mixins;
        };

        this.mqttCb = async (messageTopic, message) => {
            if (messageTopic === eventsTopic) {
                const obj: FrigateEvent = JSON.parse(message.toString());
                logger.debug(`Event received: ${JSON.stringify(obj)}`);

                const foundMixin = Object.values(this.currentMixinsMap).find(mixin => {
                    const { cameraName } = mixin.storageSettings.values;

                    return cameraName === obj.after.camera;
                });

                if (!shouldForwardByZonePrefilter(foundMixin, obj)) {
                    logger.debug(`Event skipped by zone prefilter: ${JSON.stringify({
                        camera: obj?.after?.camera,
                        zones: getEventZones(obj),
                    })}`);
                    return;
                }

                const foundMotionMixin = Object.values(this.plugin.motionDetectorDevice?.currentMixinsMap).find(mixin => {
                    const { cameraName } = mixin.storageSettings.values;

                    return cameraName === obj.after.camera;
                });

                if (foundMotionMixin) {
                    const { reportMotionOnlyOnDetection } = foundMotionMixin.storageSettings.values;

                    if (reportMotionOnlyOnDetection) {
                        await foundMotionMixin.onFrigateMotionEvent('ON');
                    }
                }

                const isNew = obj.type === 'new';
                const isEnd = obj.type === 'end';

                const hasSnapshot = isNew ? !!obj.after.has_snapshot :
                    isEnd ? !!obj.before.has_snapshot :
                        !!obj.after.has_snapshot;
                const hasClip = isNew ? !!obj.after.has_clip :
                    isEnd ? !!obj.before.has_clip :
                        !!obj.after.has_clip;
                const isFalsePositive = obj.before.false_positive || obj.after.false_positive;

                if (isFalsePositive) {
                    logger.debug(`Event skipped: ${JSON.stringify({
                        obj,
                        hasSnapshot,
                        isFalsePositive,
                        hasClip
                    })}`);
                    return;
                }

                if (foundMixin) {
                    await foundMixin.onFrigateDetectionEvent(obj);
                }
            }

            // frigate/<zone_name>/<object_name>/active
            // frigate/<camera_name>/<object_name>/active
            const matchActive = /^frigate\/([^/]+)\/([^/]+)\/active$/.exec(messageTopic);
            if (matchActive) {
                const name = matchActive[1];
                const objectName = matchActive[2];
                const activeCount = parseMqttCountPayload(message);
                if (activeCount === undefined) {
                    logger.debug(`Active topic payload ignored (unparsed): ${JSON.stringify({
                        messageTopic,
                        payload: message?.toString?.(),
                    })}`);
                    return;
                }

                // If the first segment matches a configured camera name, treat it as a camera topic.
                // Otherwise, treat it as a zone topic.
                const cameraMixin = Object.values(this.currentMixinsMap).find(mixin => {
                    const { cameraName } = mixin.storageSettings.values;
                    return cameraName === name;
                });

                if (cameraMixin) {
                    cameraMixin.onFrigateCameraObjectCountsUpdate(objectName, { active: activeCount });
                    logger.log(`Camera object active updated: ${JSON.stringify({
                        cameraName: name,
                        objectName,
                        active: activeCount,
                    })}`);
                } else {
                    const zoneName = name;
                    const zoneMixins = getCameraMixinsForZone(zoneName);

                    if (!zoneMixins.length) {
                        logger.debug(`Zone active update ignored (no camera found for zone): ${JSON.stringify({
                            zoneName,
                            objectName,
                            active: activeCount,
                        })}`);
                        return;
                    }

                    for (const mixin of zoneMixins) {
                        mixin.onFrigateCameraZoneObjectCountsUpdate(zoneName, objectName, { active: activeCount });
                    }

                    logger.debug(`Zone object active updated (routed to cameras): ${JSON.stringify({
                        zoneName,
                        objectName,
                        active: activeCount,
                        cameras: zoneMixins.map(m => m.storageSettings.values.cameraName),
                    })}`);
                }
            }

            // frigate/<zone_name>/<object_name> OR frigate/<camera_name>/<object_name>
            const matchTotal = /^frigate\/([^/]+)\/([^/]+)$/.exec(messageTopic);
            if (matchTotal) {
                const name = matchTotal[1];
                const objectName = matchTotal[2];

                // Avoid catching global topics like frigate/events.
                if (name === 'events')
                    return;

                const totalCount = parseMqttCountPayload(message);
                if (totalCount === undefined)
                    return;

                const cameraMixin = Object.values(this.currentMixinsMap).find(mixin => {
                    const { cameraName } = mixin.storageSettings.values;
                    return cameraName === name;
                });

                if (cameraMixin) {
                    cameraMixin.onFrigateCameraObjectCountsUpdate(objectName, { total: totalCount });
                    logger.debug(`Camera object total updated: ${JSON.stringify({
                        cameraName: name,
                        objectName,
                        total: totalCount,
                    })}`);
                } else {
                    const zoneName = name;
                    const zoneMixins = getCameraMixinsForZone(zoneName);
                    if (!zoneMixins.length)
                        return;

                    for (const mixin of zoneMixins) {
                        mixin.onFrigateCameraZoneObjectCountsUpdate(zoneName, objectName, { total: totalCount });
                    }

                    logger.debug(`Zone object total updated (routed to cameras): ${JSON.stringify({
                        zoneName,
                        objectName,
                        total: totalCount,
                        cameras: zoneMixins.map(m => m.storageSettings.values.cameraName),
                    })}`);
                }
            }
        };

        await mqttClient?.subscribe([eventsTopic, activeTopicWildcard, objectCountTopicWildcard], this.mqttCb);
    }

    async putSetting(key: string, value: SettingValue): Promise<void> {
        return this.storageSettings.putSetting(key, value);
    }

    private async setupMqttClient() {
        const { useMqttPluginCredentials, pluginEnabled } = this.plugin.storageSettings.values;
        if (pluginEnabled) {
            this.initializingMqtt = true;
            const logger = this.getLogger();

            if (this.mqttClient) {
                this.mqttClient.disconnect();
                this.mqttClient = undefined;
            }

            try {
                this.mqttClient = await getMqttBasicClient({
                    logger,
                    useMqttPluginCredentials,
                    mqttHost: this.plugin.storageSettings.getItem('mqttHost'),
                    mqttUsename: this.plugin.storageSettings.getItem('mqttUsename'),
                    mqttPassword: this.plugin.storageSettings.getItem('mqttPassword'),
                    clientId: 'scrypted_frigate_object_detectoor',
                });
            } catch (e) {
                logger.log('Error setting up MQTT client', e);
            } finally {
                this.initializingMqtt = false;
            }
        }
    }

    async getMqttClient() {
        if (!this.mqttClient && !this.initializingMqtt) {
            await this.setupMqttClient();
        }

        return this.mqttClient;
    }

    getLogger() {
        if (!this.logger) {
            this.logger = getBaseLogger({
                console: this.console,
                storage: this.storageSettings,
            });
        }

        return this.logger;
    }

    async getSettings() {
        try {
            const settings = await this.storageSettings.getSettings();
            return settings;
        } catch (e) {
            this.getLogger().log('Error in getSettings', e);
            return [];
        }
    }

    async canMixin(type: ScryptedDeviceType, interfaces: string[]): Promise<string[]> {
        if ((type === ScryptedDeviceType.Camera || type === ScryptedDeviceType.Doorbell) &&
            (interfaces.includes(ScryptedInterface.VideoCamera) || interfaces.includes(ScryptedInterface.Camera))) {
            return [
                ScryptedInterface.Settings,
                ScryptedInterface.ObjectDetector,
                ScryptedInterface.Sensors,
                FRIGATE_OBJECT_DETECTOR_INTERFACE
            ];
        }

        return undefined;
    }

    async getMixin(mixinDevice: any, mixinDeviceInterfaces: ScryptedInterface[], mixinDeviceState: WritableDeviceState): Promise<any> {
        return new FrigateBridgeObjectDetectorMixin({
            mixinDevice,
            mixinDeviceInterfaces,
            mixinDeviceState,
            mixinProviderNativeId: this.nativeId,
            group: 'Frigate Object Detector',
            groupKey: 'frigateObjectDetector',
        }, this)
    }

    async releaseMixin(id: string, mixinDevice: any): Promise<void> {
        await mixinDevice.release();
    }
}

